import Link from "next/link";
import cases from "@/data/mock_cases.json";

type CaseStatus =
  | "PENDING_REVIEW"
  | "APPROVED"
  | "REJECTED"
  | "NEEDS_INFO"
  | "CLOSED";

type CaseRecord = {
  id: string;
  status: CaseStatus;
  created_at: string;
  audit_trail?: Array<{ ts: string; actor: string; action: string; detail?: string }>;
};

function cx(...parts: Array<string | false | null | undefined>) {
  return parts.filter(Boolean).join(" ");
}

export default function HomePage() {
  const all = (cases as any as CaseRecord[]).slice();

  const counts = {
    pending: all.filter((c) => c.status === "PENDING_REVIEW").length,
    approved: all.filter((c) => c.status === "APPROVED").length,
    rejected: all.filter((c) => c.status === "REJECTED").length,
  };

  const activity = all
    .flatMap((c) =>
      (c.audit_trail ?? []).map((a) => ({
        case_id: c.id,
        ts: a.ts,
        actor: a.actor,
        action: a.action,
        detail: a.detail ?? "",
      }))
    )
    .sort((a, b) => (a.ts < b.ts ? 1 : -1))
    .slice(0, 6);

  const kpiCard = (title: string, value: number, pill: string, pillClass: string) => (
    <div className="rounded-xl border border-border bg-background/40 p-5 shadow-sm">
      <div className="flex items-start justify-between gap-3">
        <div>
          <div className="text-sm font-semibold">{title}</div>
          <div className="mt-6 text-3xl font-semibold tabular-nums">{value}</div>
        </div>
        <span className={cx("rounded-full border px-2 py-1 text-xs font-semibold", pillClass)}>
          {pill}
        </span>
      </div>
    </div>
  );

  return (
    <div className="min-h-screen">
      <div className="mx-auto w-full max-w-[1600px] px-6 py-10">
        <div className="flex items-start justify-between gap-4">
          <div>
            <h1 className="text-3xl font-semibold tracking-tight">ATLAS HITL UI</h1>
            <p className="mt-2 text-sm text-muted-foreground">
              Human-in-the-loop governance for high-impact tool calls. Review. Decide. Audit.
            </p>
          </div>

          <div className="flex items-center gap-2">
            <Link
              href="/cases"
              className="rounded-md border border-border bg-background/60 px-3 py-2 text-sm hover:bg-background"
            >
              Inbox
            </Link>
            <Link
              href="/settings"
              className="rounded-md border border-border bg-background/60 px-3 py-2 text-sm hover:bg-background"
            >
              Settings
            </Link>
          </div>
        </div>

        <div className="mt-6 grid grid-cols-1 gap-4 md:grid-cols-3">
          {kpiCard("Pending", counts.pending, "Needs action", "bg-slate-700/60 text-slate-100 border-slate-600/60")}
          {kpiCard("Approved", counts.approved, "OK", "bg-green-700/80 text-white border-green-600/60")}
          {kpiCard("Rejected", counts.rejected, "Blocked", "bg-red-800/80 text-white border-red-700/60")}
        </div>

        <div className="mt-6 grid grid-cols-1 gap-4 lg:grid-cols-3">
          <div className="lg:col-span-2 rounded-xl border border-border bg-background/40 p-6 shadow-sm">
            <div className="flex items-start justify-between gap-4">
              <div>
                <div className="text-sm font-semibold">Start review</div>
                <div className="mt-2 text-sm text-muted-foreground">
                  Open the inbox to review escalations, approve actions, and write audit notes.
                </div>
              </div>

              <Link
                href="/cases"
                className="rounded-md bg-foreground px-4 py-2 text-sm font-semibold text-background hover:opacity-90"
              >
                Go to Review Inbox
              </Link>
            </div>

            <div className="mt-5 grid grid-cols-1 gap-3 md:grid-cols-3">
              <div className="rounded-lg border border-border bg-background/50 p-4">
                <div className="text-xs font-semibold text-muted-foreground">Shortcuts</div>
                <div className="mt-2 text-sm">
                  J/K to move
                  <br />
                  A approve
                  <br />
                  R reject
                  <br />
                  I request info
                  <br />
                  Esc close preview
                </div>
              </div>

              <div className="rounded-lg border border-border bg-background/50 p-4">
                <div className="text-xs font-semibold text-muted-foreground">Queue hygiene</div>
                <div className="mt-2 text-sm">
                  Work pending first.
                  <br />
                  Leave a note for unusual approvals.
                  <br />
                  Treat policy refs as requirements.
                </div>
              </div>

              <div className="rounded-lg border border-border bg-background/50 p-4">
                <div className="text-xs font-semibold text-muted-foreground">System status</div>
                <div className="mt-3 flex flex-wrap gap-2">
                  <span className="rounded-full border border-green-600/60 bg-green-700/80 px-2 py-1 text-xs font-semibold text-white">
                    API: OK
                  </span>
                  <span className="rounded-full border border-green-600/60 bg-green-700/80 px-2 py-1 text-xs font-semibold text-white">
                    Audit: OK
                  </span>
                  <span className="rounded-full border border-yellow-500/60 bg-yellow-500/80 px-2 py-1 text-xs font-semibold text-black">
                    Policy store: Mock
                  </span>
                </div>
              </div>
            </div>
          </div>

          <div className="rounded-xl border border-border bg-background/40 p-6 shadow-sm">
            <div className="text-sm font-semibold">Recent activity</div>
            <div className="mt-2 text-sm text-muted-foreground">
              Latest audit events across cases.
            </div>

            <div className="mt-4 space-y-2">
              {activity.length === 0 ? (
                <div className="text-sm text-muted-foreground">No activity yet.</div>
              ) : (
                activity.map((a) => (
                  <div
                    key={`${a.ts}-${a.case_id}-${a.action}`}
                    className="rounded-lg border border-border bg-background/50 p-3"
                  >
                    <div className="flex items-start justify-between gap-3">
                      <div className="text-xs text-muted-foreground">{a.ts}</div>
                      <Link
                        href={`/cases/${encodeURIComponent(a.case_id)}`}
                        className="text-xs underline decoration-foreground/30 underline-offset-4 hover:opacity-80"
                      >
                        {a.case_id}
                      </Link>
                    </div>
                    <div className="mt-1 text-sm font-semibold">
                      {a.actor}: {a.action}
                    </div>
                    {a.detail ? (
                      <div className="mt-1 text-xs text-muted-foreground break-words">
                        {a.detail}
                      </div>
                    ) : null}
                  </div>
                ))
              )}
            </div>

            <div className="mt-4 text-xs text-muted-foreground">
              Note: This is seeded from mock data. If your API has an in-memory store, wire this to /api/cases later.
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
